.container {
    width: 60%;
    max-height: 80vh;
    padding: 2rem;
    box-sizing: border-box;
    background: rgba(255, 255, 255, 0.25);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    backdrop-filter: blur(2px);
    -webkit-backdrop-filter: blur(2px);
    border-radius: 15px;
    border: 1px solid rgba(255, 255, 255, 0.18);
    display: grid;
    grid-template-columns: 7fr 8fr;
    grid-template-rows: 1fr 10fr;
    grid-template-areas:
        "comic-button logo"
        "left-column right-column";
    gap: 1rem;
    margin: 0 auto;
}

.comic-button {
    grid-area: comic-button;
    font-family: "Madimi One", cursive;
    font-size: 38px;
    font-weight: bold;
    color: white;
    background-color: rgb(233, 179, 132);
    border: 2px solid rgb(124, 157, 150);
    border-radius: 10px;
    box-shadow: 8px 8px 0px rgb(145, 207, 205);
    transition: all 0.3s ease;  
}

.comic-button:hover {
    background-color: white;
    color: rgb(145, 207, 205);
    border: 2px solid rgb(124, 157, 150);
    box-shadow: 8px 8px 0px rgb(233, 179, 132);
}

.comic-button:active {
    background-color: #fcf414;
    box-shadow: none;
    transform: translateY(4px);
}

.left-column {
    grid-area: left-column;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    border: 2px solid #FFE8CB;
}

.name {
    margin: 1rem;
    color: wheat;
    font-size: 52px;
}

.logo {
    grid-area: logo;
    width: 100%;
}

.right-column {
    grid-area: right-column;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 26px;
    text-wrap: wrap;
    text-align: center;
    border: 2px solid #FFE8CB;
}

.desc {
    width: 100%;
    margin-top: 1rem;
}
