* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body, #root {
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

body {
  background: linear-gradient(135deg, rgb(124, 157, 150) 0%, rgb(236, 187, 143) 100%);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  font-family: "Josefin Sans", sans-serif;
  color: ivory;
  display: flex;
  align-items: center;
  justify-content: center;
}
