.flip-card {
    background-color: transparent;
    width: 60%;
    max-height: 80%;
    aspect-ratio: 4/5;
    position: relative;
    perspective: 1000px;
    box-sizing: border-box;
    transform-style: preserve-3d;
    margin: 1rem 0;
    border-radius: 15px;
}

.flip-loaded {
    opacity: 1;
}

.flip-loaded .flip-card-inner {
    animation: initialFlip 1.2s ease-in 1s;
}

@keyframes initialFlip {
    0% { transform: rotateY(0); }
    50% { transform: rotateY(180deg); }
    100% { transform: rotateY(0); }
}

.flip-card-inner {
    position: absolute;
    width: 100%;
    height: 100%;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    border-radius: 15px;
}

.flip-card:hover .flip-card-inner {
    transform: rotateY(180deg);
}

.flip-card-front, .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    box-sizing: border-box;
    border-radius: 15px;
}

.flip-card-front {
    background-image: url('https://i.imgur.com/zav9tkg.jpg'); 
    background-position: center;
    background-size: cover;
}

.flip-card-back {
    background-image: url('https://i.imgur.com/wUtHFy9.jpg'); 
    background-size: cover;
    background-position: bottom;
    transform: rotateY(180deg);
}
