.square {
  background: rgb(233, 179, 132);
  border: 2px solid rgb(124, 157, 150);
  float: left;
  font-size: 50px;
  font-weight: bold;
  line-height: 50px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 60px;
  height: 60px;
  color: rgb(124, 157, 150);
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
}

.status {
  font-size: 30px;
  margin: 2rem 0;
}

.game {
  margin: 2.5rem 0;
  max-height: 60%;
  display: flex;
  flex-direction: row;
  gap: 2rem;
}

.game-info {
  margin: 1rem 0;
}

.time-travel-btn {
  padding: 10px;
  font-family: "Madimi One", cursive;
  font-size: 30px;
  font-weight: bold;
  color: white;
  background-color: rgb(233, 179, 132);
  border: 2px solid rgb(124, 157, 150);
  border-radius: 10px;
  box-shadow: 8px 8px 0px rgb(145, 207, 205);
  transition: all 0.3s ease-in-out; 
}

.time-travel-btn:hover {
  background-color: white;
  color: rgb(145, 207, 205);
  border: 2px solid rgb(124, 157, 150);
  box-shadow: 8px 8px 0px rgb(233, 179, 132);
}

.time-travel-btn:active {
  background-color: #fcf414;
  box-shadow: none;
  transform: translateY(4px);
}

